* {
    margin: 0;
    padding: 0;
}

html,
body {
    overflow: hidden;
}

#webgl {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
    image-rendering: pixelated;
}

#filemp3 {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100px;
    height: 100px;
    image-rendering: pixelated;
}